      </div>
<!--      头像区域-->
      <div class="avatar-box">
<!--        左边图片-->
        <div class="avatar-img-box">
          <img :src="avatar? url+avatar :require('../../assets/image/user.jpg')" alt="" class="avatar-img">
          <!-- <img class="avatar-img" src="http://img1.imgtn.bdimg.com/it/u=3873278591,2846033136&fm=26&gp=0.jpg" alt=""> -->
        </div>
        <div class="avatar-right-box">
