              <img class="avatar-right-img" src="../../assets/image/my/little_bell.png" alt="" style="width: 0.3rem; height: 0.3rem;vertical-align: middle">
              <span class="avatar-right-text" style="font-size: 0.24rem;">个人隐私保障中</span>
            </div>
          <!-- </div> -->
        </div>
      </div>
<!--      分割线-->
      <div class="dividing-line"></div>
      <div class="main-box">
<!--      个人认证-->
        <div class="item">
          <div style="display: flex;justify-content: space-between;">
            <div class="per-auth">
              <img src="../../assets/image/dui.png" alt="" style="width: 0.36rem;height: 0.36rem;vertical-align: middle;">
              <span style="font-size: 0.32rem;margin-left: 0.14rem;color: #DADADD">初级认证</span>
            </div>
            <div class="right-btn" @click="toPrimaryCertification()">{{primary}}</div>
          </div>
          <div class="explain">
            <p>完成个人认证您可以获得以下权益：</p>
            <p>数字货币提现额度增加至 100 BTC</p>
            <p>提升特定法币渠道的入金额度</p>
          </div>
        </div>
<!--      附加验证-->
        <div class="item">
          <div style="display: flex;justify-content: space-between;">
