              <img src="../../assets/image/dui.png" alt="" style="width: 0.36rem;height: 0.36rem;vertical-align: middle;">
              <span style="font-size: 0.32rem;margin-left: 0.14rem;color: #DADADD">高级认证</span>
            </div>
            <div class="right-btn" @click="toAdvancedCertification()">{{senior}}</div>
          </div>
          <div class="explain">
            <p>填写居住地信息，您可获得以下权益：</p>
            <p>进一步提升特定法币渠道的入金金额</p>
          </div>
        </div>
      </div>
