</template>

<script>
import Vue from 'vue'
import { Toast } from 'vant'
import NavHead from '@/components/head.vue'
Vue.use(Toast)
export default {
  components: { NavHead },
  data () {
    return {
      url: this.GLOBAL.domainName,
      primary: '立即认证', // 初级认证
      senior: '立即认证', // 高级认证
      curHeight: '',
      isShow: false,
      title: '身份认证', // 身份认证
      isIdentity: true,
      authenticate: '', // 验证状态
      avatar: '',
      username: '',
      login_time: ''
    }
  },
  created () {
    this.authenticate = this.$route.query.authenticate
    this.avatar = this.$route.query.avatar
    this.username = this.$route.query.username
    this.login_time = this.$route.query.login_time
    // authenticate: authenticate,
    //       avatar: that.information.avatar,
    //       username: that.information.username,
    //       login_time: that.information.login_time
    console.log('打印传过来的值', this.$route.query.authenticate, typeof (this.$route.query.authenticate))
    // 0未认证 1初级认证审核 2初级认证通过 3高级认证审核 4高级认证通过 5初级审核失败 6高级审核失败
    if (this.authenticate === '0' || this.authenticate === '1' || this.authenticate === '5') {
      this.primary = '立即认证'
      this.isIdentity = true
    } else {
      this.primary = '审核通过'
      this.isIdentity = false
    }
    this.beforeMount()
  },
  methods: {
    add0 (m) { return m < 10 ? '0' + m : m },
    format (isTime) {
      var times = isTime * 1000
      var time = new Date(times)
      var y = time.getFullYear()
      var m = time.getMonth() + 1
      var d = time.getDate()
      // var h = time.getHours()
      // var mm = time.getMinutes()
      // var s = time.getSeconds()
      // console.log(y + '-' + this.add0(m) + '-' + this.add0(d) + ' ' + this.add0(h) + ':' + this.add0(mm) + ':' + this.add0(s))
      return y + '-' + this.add0(m) + '-' + this.add0(d)
    },
    beforeMount (height) {
      var h = document.documentElement.clientHeight || document.body.clientHeight
      this.curHeight = h
      console.log(this.curHeight, 'this.curHeight')
    },
    toPrimaryCertification () {
      this.$router.push({
        path: '/personal/primaryCertification',
        query: {
          authenticate: this.authenticate
        }
      })
    },
    toAdvancedCertification () {
      if (this.isIdentity) {
        console.log('adaa', this.isIdentity, '组织了')
        this.$toast({
          message: '请先认证初级认证',
          duration: 1500
        })
        return
      } else {
        console.log('adaa', this.isIdentity)
      }
      this.$router.push({
        path: '/personal/advancedCertification',
        query: {
          authenticate: this.authenticate
        }
      })
    }
  }
}
</script>
